import { ChangeEvent, FC, useEffect, useState } from 'react';
import styles from './mint-modal.module.scss';
import { LoadingOutlined } from '@ant-design/icons';
import Input from 'antd/lib/input';
import { bannerHeight, bannerWidth } from '../../banner-templates/templateVars';

interface Props {
  onCancel: () => void;
}

export const MintModal: FC<Props> = ({ onCancel }) => {
  const [name, setName] = useState<string>('');
  const [url, setUrl] = useState<string>('');
  const [description, setDescription] = useState<string>('');
  const [creating, setCreating] = useState<boolean>(true);
  const mintDisabled = name === '' || url === '' || description === '';

  useEffect(() => {
    const svg = document.querySelector('svg[class*="banner-svg"]');
    if (svg !== null) {
      const blob = new Blob([svg.outerHTML], {
        type: 'image/svg+xml;charset=utf-8',
      });
      const blobURL = URL.createObjectURL(blob);

      const image = new Image();

      image.onload = () => {
        const canvas = document.createElement('canvas');
        canvas.width = bannerWidth;
        canvas.height = bannerHeight;
        const context = canvas.getContext('2d');
        context?.drawImage(image, 0, 0, bannerWidth, bannerHeight);
        const jpg = canvas.toDataURL('image/jpg');
        console.log(jpg);
        setUrl(jpg);
      };

      image.src = blobURL;
      console.log(image);
    }
  }, []);

  useEffect(() => {
    setCreating(url === '');
  }, [url]);

  const onChange =
    (fn: (val: string) => void) =>
    ({ target }: ChangeEvent<HTMLInputElement>) =>
      fn(target.value);

  if (creating) {
    return (
      <div className={styles.creating}>
        <LoadingOutlined />
      </div>
    );
  }

  return (
    <div className={styles.MintModal}>
      <div className={styles.preview}>
        <img src={url} />
      </div>

      <div className={styles.form}>
        <div className={styles.container}>
          <div className={styles.label}>Name</div>
          <Input
            value={name}
            onChange={onChange(setName)}
            placeholder="Set NFT name"
          />
        </div>
        <div className={styles.container}>
          <div className={styles.label}>Description</div>
          <Input
            value={description}
            onChange={onChange(setDescription)}
            placeholder="Set NFT description"
          />
        </div>
      </div>

      <div className={styles.footer}>
        <button className={styles.cancel} onClick={onCancel}>
          Cancel
        </button>
        <button className={styles.mint} disabled={mintDisabled}>
          Mint NFT
        </button>
      </div>
    </div>
  );
};
